import s from "./Paginator.module.css";
import React, {useState} from "react";

const Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {


    let pagesCount = Math.ceil(totalItemsCount / pageSize);

    let pages = [];

    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);

    let leftPositionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPositionPageNumber = portionNumber * portionSize


    return (


        <div>
            {portionNumber > 1 &&
            <button onClick={() => {
                setPortionNumber(portionNumber - 1)
            }}>Prev</button>
            }
            {pages.filter(p => p >= leftPositionPageNumber && p <= rightPositionPageNumber)
                .map(p => {

                    return (

                        <button key={p}  className={s.page +' ' + (currentPage === p && s.selected)}
                                onClick={() => {onPageChanged(p)}}>{p}
                        </button>
                    )
                })}
            {portionCount > portionNumber &&
            <button onClick={() => {
                setPortionNumber(portionNumber + 1)
            }}>Next</button>
            }

        </div>


    )
}

export default Paginator;