import s from "./Users.module.css";
import React from "react";
import {NavLink} from "react-router-dom";

const User = ({user, isFollowingInProgress, unfollow, follow}) => {


    return (
        <div className={s.user} key={user.id}>
            <div className={s.imgButton}>
                <NavLink to={'/profile/' + user.id}>
                    <img
                        src={user.photos.small !== null ? user.photos.small : 'https://caronrentals.com/images/user.png'}
                        alt=""/>
                </NavLink>

                {user.followed ?
                    <button disabled={isFollowingInProgress.some(id => id === user.id)} onClick={() => {
                        unfollow(user.id)
                    }}>Unfollow</button>
                    :
                    <button disabled={isFollowingInProgress.some(id => id === user.id)} onClick={() => {
                        follow(user.id)

                    }}>Follow</button>}
            </div>
            <div>
                <div className="name">
                    {user.name}
                </div>
                <div className="status">
                    {user.status}
                </div>

            </div>
        </div>
    )


}

export default User;